import { connect } from 'react-redux'
import { emit_addTovarBasket } from '../actions/actions'
import Tovar from '../components/com_tovar.jsx'

const mapStateToProps = (state) => {

    return {
        //tovar: state.reducTovarBasket.tovar
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onAddBasket: (tovar, count) => {
            dispatch(emit_addTovarBasket(tovar, count));
        }
    }
}



// –ù–∞–∫–æ–Ω–µ—Ü, –≤—ã–∑–æ–≤–æ–º connect() –º—ã —Å–æ–∑–¥–∞–µ–º VisibleTodoList –∏ –ø–µ—Ä–µ–¥–∞–µ–º —ç—Ç–∏ –¥–≤–µ —Ñ—É–Ω–∫—Ü–∏–∏:
const ContTovar = connect(mapStateToProps, mapDispatchToProps)(Tovar)

export default ContTovar


