import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class AdminPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            modal: false
        };
        // this.state = { name: "123", price: "321" };

        //this.nameInput = this.nameInput.bind(this);
        //this.priceInput = this.priceInput.bind(this);
        this.toggle = this.toggle.bind(this);

    }

    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }

    /*
    nameInput(e) {
        //let val = e.target.value;
        //this.setState({ name: val });
    }
    priceInput(e) {

    }
    */

    /*

    */


    render() {

        return (
            <div className="container">
                <div className="row m-2 justify-content-center">
                    <label className="col-2 col-form-label">–Ω–∞–∑–≤–∞ —Ç–æ–≤–∞—Ä—É:</label>
                    <div className="col-3">
                        <input className="form-control" type="text" id="name-tovar" />
                    </div>
                </div>

                <div className="row m-2 justify-content-center">
                    <label className="col-2 col-form-label">—Ü—ñ–Ω–∞:</label>
                    <div className="col-3">
                        <input className="form-control" type="number" id="price-input" />
                    </div>
                </div>


                <div className="row col-12 justify-content-center">
                    <Button onClick={

                        () => {

                            let tovar_name = document.getElementById("name-tovar").value;
                            let tovar_price = document.getElementById("price-input").value;
                            if (tovar_name && tovar_price) {
                                this.props.onAddTovar(tovar_name, tovar_price, "");
                                document.getElementById("name-tovar").value = "";
                                document.getElementById("price-input").value = "";
                            } else {
                                this.toggle();
                            }


                        }
                    }
                        className={"bg-success m-2"} >
                        –ó–ë–ï–†–ï–ì–¢–ò
                </Button>



                    <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                        <ModalHeader toggle={this.toggle}>–ü–û–ú–ò–õ–ö–ê</ModalHeader>
                        <ModalBody>
                            –Ω–µ –∑–∞–ø–æ–≤–Ω–µ–Ω—ñ –≤—Å—ñ –ø–æ–ª—è
                        </ModalBody>
                        <ModalFooter>
                            <Button color="secondary" onClick={this.toggle}>OK</Button>
                        </ModalFooter>
                    </Modal>

                </div >
            </div >
        );
    }
}


export default AdminPage;





