import React, { Component } from 'react';
import ContTovar from '../controlers/con_tovar';
import BasketItem from './com_basket_item.jsx';
import ContBasketItem from '../controlers/con_basket_item';


class BasketPage extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {

        let arr = [];


        if (this.props.basket.tovar) {
            let s, l, item, keys = Object.keys(this.props.basket.tovar);
            for (s = 0, l = keys.length; s < l; s++) {
                item = this.props.basket.tovar[keys[s]];
                arr.push(<ContBasketItem key={s} onDecTovar={this.props.onDecTovar} onIncTovar={this.props.onIncTovar} index={s + 1} obj_key={keys[s]} tovar_in={item} />);
            }
        }

        let sort_func, sort_type = this.props.basket.sort_type ? 1 : -1;

        switch (this.props.basket.sort) {
            case 0:
                sort_func = (a, b) => {
                    if (a.props.tovar_in.tovar.name.toLowerCase() > b.props.tovar_in.tovar.name.toLowerCase())
                        return sort_type * 1;
                    if (a.props.tovar_in.tovar.name.toLowerCase() < b.props.tovar_in.tovar.name.toLowerCase())
                        return sort_type * -1;
                    else
                        return 0;
                };
                break;
            case 1:
                sort_func = (a, b) => { return sort_type * (a.props.tovar_in.tovar.price - b.props.tovar_in.tovar.price) };
                break;
            case 2:
                sort_func = (a, b) => { return sort_type * (a.props.tovar_in.count - b.props.tovar_in.count) };
                break;
            case 3:
                sort_func = (a, b) => { return sort_type * (a.props.tovar_in.tovar.price * a.props.tovar_in.count - b.props.tovar_in.tovar.price * b.props.tovar_in.count) };
                break;
            default:
                //sort_func = (a, b) => { return sort_type * (a.props.tovar_in.count - b.props.tovar_in.count) };
                break;
        }

        arr.sort(sort_func);

        return <div className={"container "}>
            <div className={"row justify-content-start"}>

                <table className="table table-striped table-bordered">
                    <thead>
                        <tr className="table-warning">
                            <th>#</th>
                            <th onClick={() => this.props.onSort(0)}>–ù–ê–ó–í–ê</th>
                            <th onClick={() => this.props.onSort(2)}>–ö–Ü–õ–¨–ö–Ü–°–¢–¨</th>
                            <th onClick={() => this.props.onSort(1)}>–¶–Ü–ù–ê</th>
                            <th onClick={() => this.props.onSort(3)}>–°–£–ú–ê</th>
                            <th>–£–ü–†–ê–í–õ–Ü–ù–ù–Ø</th>
                        </tr>
                    </thead>

                    <tbody>

                        {arr}

                    </tbody>
                </table>

            </div>

            {
                this.props.basket.total_count > 0 ? (
                    < div className={"row justify-content-end"}>
                        <button onClick={this.props.onBuy} className={"btn btn-success m-2"} type="button">
                            –ü–†–ò–î–ë–ê–¢–ò
                        </button>
                    </div>

                ) : ("")

            }

        </div>;
    }
}


export default BasketPage;


