import React, { Component } from 'react';
import { Button } from 'reactstrap';

var butStyle = {

};


class BasketItem extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {


        return <tr>
            <th scope="row">{this.props.index}</th>
            <td>{this.props.tovar_in.tovar.name}</td>
            <td>{this.props.tovar_in.count/*this.props.count*/}</td>
            <td>{this.props.tovar_in.tovar.price}</td>
            <td>{this.props.tovar_in.count * this.props.tovar_in.tovar.price}</td>
            <td>
                <div className={"d-flex justify-content-center"}>
                    <Button onClick={() => this.props.onDecTovar(this.props.obj_key)} className={"bg-danger m-2"}>
                        -
                    </Button>

                    <Button onClick={() => this.props.onIncTovar(this.props.obj_key)} className={"bg-success m-2"}>
                        +
                    </Button>

                    <Button onClick={() => this.props.onDelTovar(this.props.obj_key)} className="btn btn-outline-danger m-2 bouton-image">
                    </Button>


                </div>
            </td>
        </tr>;
    }
}

export default BasketItem;


