import { ADD_TOVAR, DEL_TOVAR } from '../actions/actions'


const tovar_list = {
    id_tovar: 100,
    tovar: {

        "1": {

            name: "tovar 1",
            price: 10,
            descript: ""
        },
        "2": {

            name: "tovar 2",
            price: 20,
            descript: ""
        },
        "3": {

            name: "tovar 3",
            price: 30,
            descript: ""
        },
        "4": {

            name: "tovar 4",
            price: 40,
            descript: ""
        },
        "5": {

            name: "tovar 5",
            price: 50,
            descript: ""
        },
        "6": {

            name: "tovar 6",
            price: 60,
            descript: ""
        },
        "7": {

            name: "tovar 7",
            price: 70,
            descript: ""
        },
        "8": {

            name: "tovar 8",
            price: 80,
            descript: ""
        },
        "9": {

            name: "tovar 9",
            price: 90,
            descript: ""
        },
        "10": {

            name: "tovar 10",
            price: 100,
            descript: ""
        },
        "11": {

            name: "tovar 11",
            price: 110,
            descript: ""
        },
        "12": {

            name: "tovar 12",
            price: 120,
            descript: ""
        },
        "13": {

            name: "tovar 13",
            price: 130,
            descript: ""
        },
        "14": {

            name: "tovar 14",
            price: 140,
            descript: ""
        },
        "15": {

            name: "tovar 15",
            price: 150,
            descript: ""
        },
        "16": {

            name: "tovar 16",
            price: 160,
            descript: ""
        }
    }
};


export default function reducTovar(state = tovar_list, action) {

    switch (action.type) {

        case ADD_TOVAR:
            let clone = Object.assign({}, state);
            clone.id_tovar++;
            clone.tovar[clone.id_tovar] = {
                name: action.name,
                price: action.price,
                descript: action.descript
            };

            return clone;

        case DEL_TOVAR:
            return {
                bla: "bla"
            }

        default:
            return state
    }
}


