import { ADD_TOVAR_BASKET, DEL_TOVAR_BASKET, BUY, INK_TOVAR_BASKET, DEC_TOVAR_BASKET, SORT } from '../actions/actions'


const basket = {
    tovar: {},
    total_count: 0,
    sort: 3,// 0-name, 1-price, 2-count, 3-total
    sort_type: true
};



export default function reducTovarBasket(state = basket, action) {

    let clone = Object.assign({}, state);


    switch (action.type) {

        case ADD_TOVAR_BASKET:
            let tovar_key = Object.keys(action.tovar_obj)[0];
            if (clone.tovar[tovar_key]) {
                clone.tovar[tovar_key].count = clone.tovar[tovar_key].count + action.count
            } else {
                clone.tovar[tovar_key] = {
                    tovar: action.tovar_obj[tovar_key],
                    count: action.count
                }
            }

            clone.total_count = clone.total_count + action.count * action.tovar_obj[tovar_key].price;
            return clone;

        case INK_TOVAR_BASKET:
            clone.tovar[action.obj_key].count++;
            clone.total_count = clone.total_count + clone.tovar[action.obj_key].tovar.price;
            return clone;


        case DEC_TOVAR_BASKET:
            if (clone.tovar[action.obj_key].count > 0) {
                clone.tovar[action.obj_key].count--;
                clone.total_count = clone.total_count - clone.tovar[action.obj_key].tovar.price;
            }
            if (clone.tovar[action.obj_key].count < 1) {
                delete clone.tovar[action.obj_key];
            }
            return clone;

        case DEL_TOVAR_BASKET:
            clone.total_count = clone.total_count - clone.tovar[action.obj_key].count * clone.tovar[action.obj_key].tovar.price;
            delete clone.tovar[action.obj_key];
            return clone;

        case SORT:
            clone.sort = action.id;
            clone.sort_type = !clone.sort_type;
            return clone;

        case BUY:
            clone.total_count = 0;
            clone.tovar = {};
            return clone;

        default:
            return state
    }
}




