import React, { Component } from 'react';
import img_1 from '../img/cart.png';
import img_2 from '../img/cart_add.png';

import { Link } from "react-router-dom";

/*
            <nav class="navbar navbar-light bg-faded">
                <a class="navbar-brand" href="#">–¢–û–í–ê–†</a>
                <a class="navbar-brand" href="#">–ö–û–®–ò–ö</a>
            </nav>

btn-outline-success
*/
// !!!! .row —Ç–µ–ø–µ—Ä—å display:flex
//margin: '5px',
//45px
const divStyle = {
    width: "45px",
    height: "45px",
    backgroundRepeat: 'no-repeat'
};

const sImg_1 = {
    backgroundImage: 'url(' + img_1 + ')'
};

const sImg_2 = {
    backgroundImage: 'url(' + img_2 + ')'
};

class NavBar extends React.Component {

    constructor(props) {
        super(props);
        this.set_total_count = this.set_total_count.bind(this);
    }

    set_total_count(new_value) {
    }

    render() {
        return <div className={"d-flex justify-content-start"}>
            <div>
                <nav className={"navbar navbar-light bg-faded"}>
                    <div className={"form-inline"}>

                        <Link to='/'>
                            <button className={"btn btn-success m-2"} type="button">
                                –¢–û–í–ê–†
                            </button>
                        </Link>

                        <Link to='/basket'>
                            <button className={"btn  btn-info m-2"} type="button">
                                –ö–û–®–ò–ö
                        </button>
                        </Link>

                        <Link to='/admin'>
                            <button className={"btn  btn-warning m-2"} type="button">
                                –ê–î–ú–Ü–ù
                            </button>
                        </Link>

                    </div>
                </nav>
            </div>

            <div className={"d-flex justify-content-between mr-5 ml-auto"}>

                <div className={"my-auto"} style={Object.assign({}, divStyle, this.props.total_count ? sImg_2 : sImg_1)} >
                </div>

                <div className={"float-right align-self-center"}>
                    {this.props.total_count}
                </div>

            </div>

        </div>;
    }
}

export default NavBar;


