import { connect } from 'react-redux'
import { emit_setSort, emit_Buy } from '../actions/actions'
import BasketPage from '../components/com_basket_page.jsx'


const mapStateToProps = (state) => {
    return {
        basket: state.reducTovarBasket//.tovar
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSort: (id) => {
            dispatch(emit_setSort(id));
        },
        onBuy: () => {
            dispatch(emit_Buy());
        }
    }
}




const ContBasketPage = connect(mapStateToProps, mapDispatchToProps)(BasketPage)

export default ContBasketPage


