import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import { Route, Switch } from "react-router-dom";

import ContTovarPage from './controlers/con_tovar_page';
import ContNavBar from './controlers/con_navbar';
import ContBasketPage from './controlers/con_basket_page';
import ContAdminPage from './controlers/con_admin_page';



class App extends Component {
  render() {
    return (
      <div className={"App"}>
        <div>
          <h3>
            –ú–ê–ì–ê–ó–ò–ù
         </h3>
        </div>
        <ContNavBar />

        <Switch>
          <Route exact path='/' component={ContTovarPage} />
          <Route path='/basket' component={ContBasketPage} />
          <Route path='/admin' component={ContAdminPage} />
        </Switch>


      </div>
    );
  }
}

export default App;



