// signal
export const SWITCH_TAB = 'SWITCH_TAB'
export const ADD_TOVAR = 'ADD_TOVAR'
export const DEL_TOVAR = 'DEL_TOVAR'
export const ADD_TOVAR_BASKET = 'ADD_TOVAR_BASKET'
export const DEL_TOVAR_BASKET = 'DEL_TOVAR_BASKET'
export const INK_TOVAR_BASKET = 'INK_TOVAR_BASKET'
export const DEC_TOVAR_BASKET = 'DEC_TOVAR_BASKET'
export const BUY = 'BUY'
export const SORT = 'SORT'


export const emit_addTovar = (name, price, descript) => {
    return {
        type: ADD_TOVAR,
        name: name,
        price: price,
        descript: descript
    }
}
export const emit_delTovar = (id) => {
    return {
        type: DEL_TOVAR,
        id: id
    }
}

export const emit_addTovarBasket = (tovar_obj, count) => {
    return {
        type: ADD_TOVAR_BASKET,
        tovar_obj: tovar_obj,
        count: count
    }
}

export const emit_inkTovarBasket = (obj_key) => {
    return {
        type: INK_TOVAR_BASKET,
        obj_key: obj_key
    }
}

export const emit_de—ÅTovarBasket = (obj_key) => {
    return {
        type: DEC_TOVAR_BASKET,
        obj_key: obj_key
    }
}

export const emit_delTovarBasket = (obj_key) => {
    return {
        type: DEL_TOVAR_BASKET,
        obj_key: obj_key
    }
}

export const emit_setSort = (id) => {
    return {
        type: SORT,
        id: id
    }
}

export const emit_Buy = () => {
    return {
        type: BUY
    }
}




