import React, { Component } from 'react';
import { Button } from 'reactstrap';

//container-fluid
//col-xs-1 m-3
class Tovar extends React.Component {

    constructor(props) {
        super(props);
        this.state = { count: 1 };

        this.inc = this.inc.bind(this);
        this.dec = this.dec.bind(this);
        this.buy = this.buy.bind(this);
    }

    inc() {
        this.setState({ count: ++this.state.count });
    }
    dec() {
        if (this.state.count === 1) {
            return;
        }
        this.setState({ count: --this.state.count });
    }

    buy() {


        this.props.onAddBasket(this.props.sig, this.state.count);
        this.setState({ count: 1 });
    }

    render() {
        //console.log(this.props);
        //alert(888)
        return <div className={"col-xl-1 col-lg-2 col-md-3 col-sm-4 m-3 card card-body bg-light p-1"}>

            <div className={"row justify-content-center "}>
                <p><strong>
                    {this.props.sig[this.props.id].name}
                    <br />
                </strong>
                    {this.props.sig[this.props.id].price + " –≥—Ä–Ω."}
                </p>
            </div>

            <div className={"d-flex justify-content-center"}>
                <Button onClick={this.dec} className={"bg-danger m-2"}>
                    -
                </Button>
                <div className={"text-success m-2"}>
                    {this.state.count}
                </div>
                <Button onClick={this.inc} className={"bg-success m-2"}>
                    +
                </Button>
            </div>

            <div className={"row justify-content-center"}>
                <Button className={"bg-success"} onClick={this.buy}>
                    –∫—É–ø–∏—Ç–∏
                </Button>
            </div>

        </div>;
    }
}

export default Tovar;


