import React, { Component } from 'react';
import ContTovar from '../controlers/con_tovar';


class TovarPage extends React.Component {

    constructor(props) {
        super(props);
    }
    //
    render() {
        console.log(this.props);
        let s, l, item, keys = Object.keys(this.props.tovar);
        let arr = [];
        for (s = 0, l = keys.length; s < l; s++) {
            item = this.props.tovar[keys[s]];

            //console.log(keys[s])
            //alert(444444444444)
            let obj = {};
            obj[keys[s]] = item;
            arr.push(<ContTovar key={s} id={keys[s]} sig={obj
            } onAddBasket={this.props.onAddBasket} />);
        }

        return <div className="container">
            <div className={"row justify-content-start"}>
                {
                    arr
                }
            </div>
        </div >;
    }
}


export default TovarPage;



