import { connect } from 'react-redux'
import BasketItem from '../components/com_basket_item.jsx'
import { emit_inkTovarBasket, emit_delTovarBasket, emit_de—ÅTovarBasket } from '../actions/actions'



const mapStateToProps = (state, ownProps) => {

    return {
        count: state.reducTovarBasket.tovar[ownProps.obj_key].count
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onIncTovar: (tovar_obj) => {
            dispatch(emit_inkTovarBasket(tovar_obj));
        },
        onDecTovar: (tovar_obj) => {
            dispatch(emit_de—ÅTovarBasket(tovar_obj));
        },
        onDelTovar: (tovar_obj) => {
            dispatch(emit_delTovarBasket(tovar_obj));
        }
    }
}


const ContBasketItem = connect(mapStateToProps, mapDispatchToProps)(BasketItem)

export default ContBasketItem


