import { connect } from 'react-redux'
import { emit_addTovar } from '../actions/actions'
import TovarPage from '../components/com_tovar_page.jsx'

const mapStateToProps = (state) => {

    return {
        tovar: state.reducTovar.tovar
    }
}


const ContTovarPage = connect(mapStateToProps/*, mapDispatchToProps*/)(TovarPage)

export default ContTovarPage


