import { connect } from 'react-redux'
import { emit_addTovar } from '../actions/actions'
import AdminPage from '../components/com_admin_page.jsx'


const mapDispatchToProps = (dispatch) => {
    return {
        onAddTovar: (name, price, descript) => {
            dispatch(emit_addTovar(name, price, descript));
        }
    }
}

const ContAdminPage = connect(/*mapStateToProps,*/ null, mapDispatchToProps)(AdminPage)

export default ContAdminPage


