import { combineReducers } from 'redux'
import { createStore /*, applyMiddleware*/ } from 'redux'
//import thunk from 'redux-thunk';

import reducTovar from './redTovar'
import reducTovarBasket from './redTovarBasket'
//import reducTab from './rTab'
//import { items, itemsHasErrored, itemsIsLoading } from './rAsync';

const persistedState = localStorage.getItem('reduxState') ? JSON.parse(localStorage.getItem('reduxState')) : {}

const rootReducer = combineReducers({
    reducTovar,// —Ü–µ –±—É–¥–µ –Ω–æ–≤–∏–π –∫–ª—é—á
    reducTovarBasket
    //reducBook
})


export default function configureStore(initialState = persistedState) {
    return createStore(
        rootReducer,
        initialState
    );
}



